import React from "react";
import ReactDOM from "react-dom";
import "./index.css";

function AdDate(props) {
  return (
    <div className={props.read}>
      <button className="addate" onClick={() => props.onClick()}>
      <span>
        {props.value}
      </span>
      </button>
    </div>
  );
}

class Board extends React.Component {
  renderSquare(i) {
    return (
      <AdDate
        value={this.props.squares[i]}
        read={this.props.squares[i]}
        onClick={() => this.props.onClick(i)}
      />
    );
  }

  render() {
    return (
      <div>
        <div className="board-row">
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
          {this.renderSquare(3)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
          {this.renderSquare(9)}
          {this.renderSquare(10)}
          {this.renderSquare(11)}
          {this.renderSquare(12)}
          {this.renderSquare(13)}
          {this.renderSquare(14)}
          {this.renderSquare(15)}
          {this.renderSquare(16)}
          {this.renderSquare(17)}
          {this.renderSquare(18)}
          {this.renderSquare(19)}
          {this.renderSquare(20)}
          {this.renderSquare(21)}
          {this.renderSquare(22)}
          {this.renderSquare(23)}
          {this.renderSquare(24)}
        </div>
      </div>
    );
  }
}

class Calendar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      history: [
        {
          squares: Array(25).fill(1).map((n, i) => n + i)
        }
      ],
      stepNumber: 0,
      xIsNext: true
    };
  }

  handleClick(i) {
    var history = this.state.history.slice(0, this.state.stepNumber + 1);
    var current = history[history.length - 1];
    const squares = current.squares.slice();
    

    if (squares[i] === "read") {
    return;
    }

    squares[i] = "read";

    this.setState({
      history: history.concat([
        {
          squares: squares
        }
      ]),
      stepNumber: history.length,
      xIsNext: !this.state.xIsNext
    });
  }

  jumpTo(step) {
    this.setState({
      stepNumber: step,
      xIsNext: step % 2 ? false : true
    });
  }

  render() {
    const history = this.state.history;
    const current = history[this.state.stepNumber];

    const complete = calculateComplete(current.squares);
    let status;
    if (complete) {
      status = complete;
    } 

    const moves = history.map((step, move) => {
      const desc = move ?  move + "": "";
      return (
        <li key={move}>
          <button onClick={() => this.jumpTo(move)}>{desc}</button>
        </li>
      );
    });

    return (
      <div className="calendar">
        <div className="status"><h1>{status}</h1></div>
        <div className="calendar-board">
          <Board squares={current.squares} onClick={i => this.handleClick(i)} />
        </div>
        <div className="calendar-info">
          <ul>{moves}</ul>
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(<Calendar />, document.getElementById("root"));

function calculateComplete(squares) {
  const fin = 'Congratulation';
  let count = 0;
  let tobe = squares.length;
  for (let i = 0; i < squares.length; i++) {
    if (squares[i] === "read") {
      count ++;
      tobe --;
    }
  }
  if (count >= squares.length) {
    return fin;
  }
    return tobe + " days to go before";
}



